import React from "react"
import { connect } from "react-redux"
import Loadable from 'react-loadable';
import "../components/layout.css"
import Swipe from 'react-easy-swipe';

const siteMap = [
  {name: 'about', totalPages: 2},
  {name: 'education', totalPages: 1},
  {name: 'works', totalPages: 1}
];

const About = Loadable({
  loader: () => import('../components/about'),
  loading() {
    return <div>Loading...</div>
  }
});

const Education = Loadable({
  loader: () => import('../components/education'),
  loading() {
    return <div>Loading...</div>
  }
});

const Works = Loadable({
  loader: () => import('../components/works'),
  loading() {
    return <div>Loading...</div>
  }
});

const mapStateToProps = ({ page, isSwitching, menuPage, progressPage, progressPageDisplay, totalPages, x, y }) => {
  return { page, isSwitching, menuPage, progressPage, progressPageDisplay, totalPages, x, y  }
}

const mapDispatchToProps = dispatch => {
  return { 
    openPage: (page) => {
      dispatch({ type: `SETMENUPAGE`, page }); 
      dispatch({ type: `TOGGLESWITCHPAGE` });
      setTimeout(() => {
        dispatch({ type: `SETPAGE`, page }); 
        dispatch({ type: `RESETPROGRESSPAGE` }); 
        let totalPages = siteMap.find( pages => pages.name === page ).totalPages
        dispatch({ type: `SETTOTALPAGES` , totalPages}); 
      }, 200);
      setTimeout(() => {
        dispatch({ type: `TOGGLESWITCHPAGE` });
      }, 400);
    },
    editPage: (number) => {
      dispatch({ type: `TOGGLESWITCHPAGE` });
      dispatch({ type: `EDITPROGRESSPAGE` , number });
      setTimeout(() => {
        dispatch({ type: `EDITPROGRESSPAGEDISPLAY` , number }); 
      }, 200);
      setTimeout(() => {
        dispatch({ type: `TOGGLESWITCHPAGE` });
      }, 400);
    },
    setSwipe: (x,y) => {
      dispatch({ type: `SETSWIPE` , x , y});
    }
  }
}
    
class App extends React.Component {
  constructor(props){
    super(props);
    this.onSwipeMove = this.onSwipeMove.bind(this);
    this.checkSwipe = this.checkSwipe.bind(this);
  }

  componentDidMount(){
    const self = this;
    window.addEventListener('wheel', function(e) {
      if(e.deltaY < 0 && self.props.progressPage > 1 && self.props.progressPageDisplay > 1) {
        self.props.editPage(-1);
      }
      if(e.deltaY > 0 && self.props.progressPage < self.props.totalPages && self.props.progressPageDisplay < self.props.totalPages) {
        self.props.editPage(1);
      }
    });
  }

  getContent() {
    switch(this.props.page) {
      case 'about' :
        return <About page = {this.props.progressPageDisplay}/>;
      case 'works' :
        return <Works page = {this.props.progressPageDisplay}/>;
      case 'education' :
        return <Education page = {this.props.progressPageDisplay}/>;
      default : 
        return 'ERROR PAGE NOT FOUND';
    }
  }

  onSwipeMove(position, event) {
    this.props.setSwipe(position.x,position.y)
  }

  checkSwipe() {
    if(Math.abs(this.props.y) > 40 && this.props.y < 0 && this.props.progressPage < this.props.totalPages && this.props.progressPageDisplay < this.props.totalPages) {
      this.props.editPage(1);
    } else if (Math.abs(this.props.y) > 40 && this.props.y > 0  && this.props.progressPage > 1 && this.props.progressPageDisplay > 1) {
      this.props.editPage(-1);
    }
  }

  render() {
    return ( 
      <Swipe className="app" onSwipeMove={this.onSwipeMove} onSwipeEnd={this.checkSwipe}>
        <div className="app__note">
          <strong>Welcome to my portfolio</strong><br/><br/> Email: austinnguyen01@gmail.com<br/>Discord: KyneticDerpcake#2878<br/>
        </div>
        <div className="app__container--left">
          <div className="app__container--left__title">
            <div className="app__container--left__title--first">Austin</div>
            <div className="app__container--left__title--last">Nguyen
              <div className="app__container--left__title--number">01</div>
            </div>
          </div>
          <div className="app__container--left__nav-menu">
            <span onClick={()=>{this.props.openPage('about')}} className = {this.props.menuPage == 'about' ? 'selected' : ''}>About</span>
            <span onClick={()=>{this.props.openPage('works')}} className = {this.props.menuPage == 'works' ? 'selected' : ''}>Works</span>
            <span onClick={()=>{this.props.openPage('education')}} className = {this.props.menuPage == 'education' ? 'selected' : ''}>Education</span>
          </div>
        </div>
        <div className="app__container--right">
          <div className={"app__container--right__page" + (this.props.isSwitching ? ' switch-page' : '')}>
            <div className="app__container--right__page__header">
              <span>{this.props.page}</span>
              <span>{this.props.progressPageDisplay}/{this.props.totalPages}</span>
            </div>
            {this.getContent()}
          </div>
          <div className="app__container--right__tab"></div>
        </div>
      </Swipe>
    )
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App)
